@mixin centering {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin IBM-font {
  font-family: 'IBM Plex Sans', sans-serif;
  font-style: normal;
}

@mixin onBigDesktop {
  @media (min-width: 1920px) {
    @content;
  }
}

@mixin onIPad {
  @media (max-width: 1024px) {
    @content;
  }
}

@mixin onPhone {
  @media (max-width: 786px) {
    @content;
  }
}

@mixin onIPhone {
  @media (max-width: 320px) {
    @content;
  }
}