.gallery {
  &__title {
    margin-top: 200px;
    margin-bottom: 60px;
  }

  &__image {
    width: 100%;
    object-fit: cover;
    object-position: top;
    transition: transform 0.5s;
    
    &-container {
      width: 100%;
      height: 100%;
      overflow: hidden;
    }

    &--onBigDesktop {
      display: none;
      
      @include onBigDesktop {
        display: block;
      }
    }

    &--first-row {
      height: 600px;
    }

    &--second-row {
      height: 360px;
    }

    &:hover {
      transform: scale(1.1);
    }
  }

}
