.icon {
  &__hamburger {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 3px;

    &-rectangle {
      width: 18px;
      height: 2px;
      background-color: $color-text;
      border-radius: 20%;
      transition: width 0.3s;
    }

    &:hover &-rectangle:nth-child(2) {
      width: 12px;
    }
    &:hover &-rectangle:nth-child(1) {
      width: 16px;
    }
  }

  &__cross {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 3px;

    &-rectangle {
      position: absolute;
      width: 18px;
      height: 2px;
      background-color: $color-text;
      border-radius: 20%;
      transition: transform 0.3s;

      &:nth-child(1) {
        transform: translateY(-50%) rotate(45deg);
      }

      &:nth-child(2) {
        transform: translateY(-50%) rotate(-45deg);
      }
    }
  
    &:hover &-rectangle:nth-child(1) {

    }
    &:hover &-rectangle:nth-child(2) {

    }
  }
}
