.events {
  &__title {
    margin-bottom: 60px;
  }

  &__first-exhibition {
    display: flex;
    justify-content: flex-start;
    gap: 130px;
    margin-bottom: 100px;

    @include onBigDesktop {
      align-items: center;
    }

    @include onIPad{
      flex-direction: column;
      gap: 35px;
    }

    
    @include onPhone{
      flex-direction: column;
      align-items: center;
      gap: 30px;
    }
  }

  &__second-exhibition {
    display: flex;
    flex-direction: row-reverse;
    gap: 130px;
    margin-bottom: 100px;

    @include onBigDesktop {
      align-items: center;
    }
    
    @include onIPad{
      flex-direction: column;
      gap: 35px;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;

    &-container {
      width: 670px;
      height: 520px;

      @include onBigDesktop {
        width: 870px;
      }

      @include onIPad {
        width: 100%;
      }
      
      @include onPhone {
        width: 100%;
        height: 100%;
        min-height: 210px;
      }
    }
  }

  &__content {
    margin-top: 50px;

    @include onIPad {
      margin-top: 0;
    }

    @include onPhone {
      width: 100%;
    }

    &:nth-child(3) {
      @include centering;
      width: 570px;
      height: 204px;
      background-color: #fff;
    }
  }
  
  &__sub-title {
    margin-bottom: 32px;

    @include onPhone {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0 auto;
      margin-bottom: 32px;
    }
  }

  &__event {
    @include IBM-font;
    font-weight: 500;
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;

    color: #687480;

    @include onPhone {
      font-size: 16px;
    }
  }

  &__date {
    @include IBM-font;
    font-weight: bold;
    font-size: 14px;
    line-height: 150%;

    color: $color-green;

    @include onIPad{
      margin-left: 26px;
    }

    @include onPhone {
      font-size: 16px;
    }
  }

  &__name {
    display: inline-flex;
    align-items: center;
    margin-right: 26px;
    font-weight: normal;
    font-size: 36px;

    @include onPhone {
      font-size: 28px;
      margin-right: 7px;
    }

    &-container {
      display: flex;
      align-items: center;
    }
  }
  
  &__point {
    right: 0;
    width: 15px;
    height: 15px;
    background-color: $color-red;

    @include onPhone {
      width: 10px;
      height: 10px;
    }
  }

  &__description {
    margin-top: 15px;
    width: 370px;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;

    color: #4E4E4E;

    @include onPhone {
      width: 100%;
      min-width: 280px
    }

  }
  
}