.footer {
  &__container {
    padding: 50px 0 35px;
  }

  &__networks {
    display: flex;
    gap: 20px;
    margin-top: 60px;
  }

  &__schedule {
    margin-bottom: 20px;
    @include IBM-font;
    font-weight: 400;
    font-size: 14px;
    line-height: 140.62%;
  }
  
  &__hours {
    margin-bottom: 20px;
    @include IBM-font;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
  }

  &__time {
    font-weight: 600;
  }

  &__weekends {
    @include IBM-font;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
  }

  &__weekend {
    @include IBM-font;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
  }

  &__second-column {
    @include IBM-font;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
  }

  &__contacts {
    margin-bottom: 20px;
  }

  &__separator {
    display: flex;
    justify-content: center;
  }

  &__rectangle {
    width: 1px;
    height: 270px;
    background-color: rgba(26, 90, 76, 0.5);

    @include onIPad {
      width: 100%;
      height: 1px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    list-style: none;
  }

  &__link {
    position: relative;
    display: block;
    @include IBM-font;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    text-decoration: none;
    color: $color-text;
    transition: color 0.3s;

    &:hover {
      color: $color-sand;
    }
  }
  
  &__bottom {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    @include IBM-font;
    font-weight: 300;
    font-size: 14px;
    line-height: 150%;
  }

}