.lecture {
  position: relative;
  display: flex;
  align-items: center;

  &__image {
    width: 100%;
    object-fit: cover;
    object-position: center;

    @include onIPad {
      min-height: 380px;
      margin: 0 auto;
      width: 85%;
    }

    @include onIPad {
      min-height: 210px;
      width: 100%;
    }
  }
  
  &__box {
    position: absolute;
    bottom: -100px;
    left: 50%;
    transform: translateX(-50%);
    @include centering;
    width: 100%;
    max-width: 570px;
    height: 100%;
    max-height: 205px;
    background-color: #fff;
    box-shadow: 0 2px 0 rgba(26, 90, 76, 0.25);
    z-index: 1;
    transition: bottom 0.3s;

    &:hover {
      bottom: -95px;
    }

    @include onPhone {
      width: 280px;
      height: 155px;
    }
  }

  &__sub-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  &__event {
    @include IBM-font;
    font-weight: 500;
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;

    color: #687480;
  }

  &__date {
    @include IBM-font;
    font-weight: bold;
    font-size: 14px;
    line-height: 150%;

    color: $color-green;
  }

  &__name {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 370px;

    margin-top: 23px;

    font-weight: normal;
    font-size: 36px;
    line-height: 100%;

    @include onPhone {
      font-size: 28px;
      width: 236px;
    }
  }
}
