.pointer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  width: 10px;
  margin-left: -9px;

  @include onIPad {
    display: none;
  }

  &__text {
    @include IBM-font;
    font-weight: bold;
    font-size: 14px;
    color: #687480;
    text-transform: uppercase;

    transform: rotate(-90deg);
  }

  &__rectangle {
    width: 1px;
    height: 100px;
    background-color: $color-green;
  }
}