.button {
  display: flex;
  text-decoration: none;

  transition: all 0.3s;

  &__text {
    @include centering;
    position: relative;
    width: 150px;
    height: 70px;
    border: 1px solid $color-green;

    font-weight: 500;
    font-size: 16px;
    line-height: 100%;

    color: $color-text;
    text-transform: uppercase;

    transition: border 0.3s, color 0.3s, border-right 0.3s;
    overflow: hidden;

    @include onPhone {
      width: 230px;
      height: 50px;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: -1;
      width: 150px;
      height: 70px;
      background-color: $color-green;
      transition: transform 0.3s;
      transform: translateX(100%);

      @include onPhone {
        width: 230px;
        height: 50px;
      }
    }
  }

  &__arrow {
    @include centering;
    width: 70px;
    height: 70px;
    background-color: $color-green;

    transition: background-color 0.3s;

    @include onPhone {
      width: 50px;
      height: 50px;
    }
  }

  &:hover > &__text {
    color: $color-white;
    border: 0 solid $color-green;
    border-right: $color-green;
  }

  &:hover > &__text::after {
    background-color: $color-green;
    transform: translateX(0);
  }

}