.header {
  position: relative;
  height: 100vh;

  &__photo {
    position: absolute;
    right: 0;
    height: 100vh;
    width: 55%;

    @include onPhone {
      width: 100%;
      height: $header-photo-height-onPhone;
    }
  }

  &__image {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: top left;
    z-index: 0;
  }

  &__logo {
    margin: 0 auto;
    z-index: 1;
  }
  
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 45%;
    height: 100vh - ($top-action-height + $events-height);

    @include onPhone {
      width: 100%;
    }

  }

  &__caption {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    margin-top: $header-main-margin;

    @include onPhone {
      justify-content: center;
      align-items: center;
    }

    &-container {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      @include onPhone {
        position: absolute;
        align-items: center;
        margin-top: $header-main-margin + $header-photo-height-onPhone + 10vh;
      } 
    }
  }

  &__title {
    z-index: 1;
    font-weight: 800;
    font-size: 72px;
    line-height: 100%;

    color: rgba(0, 0, 0, 1);
    text-transform: uppercase;

    &-stroke {
      position: absolute;
      margin-top: $top-action-height + $header-main-margin;
      font-weight: 800;
      font-size: 72px;
      line-height: 100%;
      color: rgba(0, 0, 0, 0);
      -webkit-text-stroke: 1px #000;
      text-transform: uppercase;
      z-index: 2;

      @include onPhone {
        display: none;
      }
    }
    
    &--onDesktop {
      @include onPhone {
        display: none;
      }
    }

    &--onPhone {
      display: none;

      @include onPhone {
        display: block;
        text-align: center;
        font-size: 45px;
        font-weight: 700;
      }
    }
  }
  
  &__button {
    margin-top: 80px;
    
    @include onIPad {
      margin-top: 40px;
    }
  }

  &__pointer {
    display: flex;
    justify-content: flex-start;
    height: $events-height;
    margin: 0 auto;
  }
}
