.menu {
  background-color: $color-white;  
  z-index: 3;
  height: 100vh;

  &__logo {
    margin: 0 auto;
  }

  &__list {
    list-style: none;
    padding: 0;
    margin-top: 50px;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    position: relative;
    display: inline-block;
    padding-bottom: 8px;
    
    @include IBM-font;
    font-weight: 400;
    font-size: 15px;
    line-height: 100%;
    color: $color-text;
    text-decoration: none;
    text-transform: uppercase;

    transition: border-bottom 0.3s;

    &::after {
      content: "";
      position: absolute;
      display: block;
      margin-top: 10px;
      width: 60%;
      height: 1px;
      background-color:$color-green;
      opacity: 0;
      transition: opacity 0.3s, width 0.3s;
    }

    &:hover::after {
      opacity: 1;
      width: 100%;
    }
  }

}
