.subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 550px;

  @include onBigDesktop {
    height: 650px;
  }
  background-image: url(../images/subscription/background.png);
  background-position: left;
  background-size: cover;
  background-repeat: no-repeat;

  &__title {
   margin-bottom: 20px;
   font-weight: bold;
   font-size: 48px;
   line-height: 100%;

   color: #fff;
  }

  &__sub-title {
    margin-bottom: 65px;
    @include IBM-font;
    font-weight: normal;
    font-size: 18px;
    line-height: 100%;

    color: #fff;
  }
  
  &__box {
    display: flex;
    gap: 30px;

    &-container {
      width: 35%;
      max-width: 570px;
    }
  }

  &__email {
    padding-left: 35px;
    width: 100%;
    height: 70px;
    border: 0;
    background-color: #fff;

    &::placeholder {
      font-weight: 200;
      font-size: 14px;
      line-height: 100%;
      letter-spacing: 1px;
      color: #687480;
    }

    &:focus {
      outline: none;
      border: 0;
    }
  }

  &__button{
    position: relative;
    @include centering;
    width: 70px;
    min-width: 70px;
    height: 70px;
    background-color: $color-red;
    border: 0;
    overflow: hidden;

    &::after {
      content: "";
      position: absolute;
      width: 70px;
      height: 70px;
      z-index: 0;
      background-color: $color-white;
      right: 0;
      transform: translateX(101%);
      transition: transform 0.3s;
    }

    &:hover::after {
      transform: translateX(0);
    }
  }

  &__icon {
    position: absolute;
    z-index: 1;
  }
}
