.page {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  color: $color-text;

  scroll-behavior: smooth;

  &__section {
    margin-top: 200px;

    @include onPhone {
      margin-top: 30px;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.5s;

    &:target {
      transform: translateX(0);
    }
  }
}